<template>
  <div class="allmusic">
    <van-nav-bar left-text="全部歌曲"  @click-left="goback" left-arrow />
    <a class="gotop" href="#top"><van-icon name="back-top" /></a>

    <div class="song-list-box">
      <div class="song-list-top" id="top">
        <div class="song-list-left">
          <div class="song-list-img-box">
            <img class="auto-img w30" src="../assets/images/mv-play.png" />
          </div>
          <div class="song-list-playall">
            播放全部<span>(总共{{ singerallindex }}首)</span>
          </div>
        </div>
      </div>
      <van-list
        class="all-music-item"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="getsongerdetail"
        offset
        ref="items"
      >
        <van-cell class="song-list-top"
        v-for="(item, index) in hotsingermusicdata"
        :key="index" @click="goPlayPage(item)">
          <div class="song-list-left">
            <div class="song-list-img-box">{{ index + 1 }}</div>
            <div class="song-list-playall">
              <div class="song-list-content">{{ item.name }}</div>
              <div class="song-list-detail">
                <span v-for="(ite,ind) in item.ar" :key="ind">{{ite.name}}</span> - {{ item.al.name }}
              </div>
            </div>
          </div>
          <!-- <div class="song-list-collect">···</div> -->
          <div @click.stop="playmusic(item)" class="playbtn"><van-icon name="play-circle-o" /></div>
        </van-cell>
      </van-list>
    </div>
  </div>
</template>

<script>
import "../assets/less/singerallmusic.less";
export default {
  data() {
    return {
      hotsingermusicdata: [],
      singerallindex:0,
      listindex: 0,
      loading: false,
      finished: false,
    };
  },
  computed: {
    //获取歌手id
    getsonglistid(){
      return this.$route.query.id
    },
  },
  watch: {
    getsonglistid(val){
      if (val) {
        this.getsongerdetail();
      }
    },

  },
  methods: {
    //返回
    goback() {
      this.$router.go(-1);
    },
    // 获取歌手详情和单曲
    getsongerdetail() {
      this.listindex = this.listindex+1;
      let count = (this.listindex - 1) * 30

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/artist/songs",
        params: {
          id: this.getsonglistid,
          limit:30,
          offset:count
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code === 200) {
            this.singerallindex = result.data.total
            if (this.listindex == 1) {
              this.hotsingermusicdata = result.data.songs;
            }else{
              this.hotsingermusicdata = [...this.hotsingermusicdata,...result.data.songs];
            }
            
            // 加载状态结束
        this.loading = false;
        // 数据全部加载完成
        if (this.hotsingermusicdata.length > this.singerallindex) {
          this.finished = true;
        }
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.error(err)
        });


    },
     //前往详情页面
    goPlayPage(item){
      this.$router.push({path:'musicDetail',query:{id:item.id,type:4,ids:this.getsonglistid}})

    },
    //播放不跳转
    playmusic(item){
      let musicData = {
        id:item.id,
      }
      this.playmusicids = this.hotsingermusicdata.map(v => {
        return v.id
      })
      this.$store.commit("setplaymusicids",this.playmusicids)
      this.$store.commit("setmusicid",musicData)
      this.$store.commit("setMusicIsPlay",true)
    },
    
  },
};
</script>